<template>
  <v-layout d-flex flex-column class=" pa-6">
    <!-- <v-flex>
      {{datetime.toString()}} <br>
      {{date}} <br>
      {{time}}
    </v-flex> -->
    <v-flex>
      <DatePicker ref="date" dense outlined prependInnerIcon='mdi-calendar'  v-model="date" :value="datetime.toISOString().split('T')[0]" />
    </v-flex>
    <v-flex class="align-center  d-flex">
      <VTimePicker   format="24hr" class="mx-auto" v-model="time"  use-seconds/>
    </v-flex>
  </v-layout>
</template>

<script>
import DatePicker from './DatePicker'
import utility from './../utility'
const { pad } = utility
export default {
  props: ['value'],
  components: {
    DatePicker
  },
  data: () => ({
    datetime: new Date(),

  }),
  watch: {
    datetime (d) {
      this.$emit('input', d)
    }
  },
  methods: {
    updateTime (hours,minuts,seconds) {
      this.datetime =  new Date(this.datetime.getFullYear(), this.datetime.getMonth(), this.datetime.getDate(), hours, minuts, seconds)
    },
    updateDate (year,month,date) {
        this.datetime = new Date(year, month, date, this.datetime.getHours(), this.datetime.getMinutes(), this.datetime.getSeconds())
    }
  },
  computed: {
    date: {
      get () {
        return [this.datetime.getFullYear(), pad(this.datetime.getMonth()+1), pad(this.datetime.getDate())].join('-')
      },
      set (value) {
        const [ year, month, date ] = value.split('-')
        this.updateDate(year, Number(month)-1, date);
      }
    },
    time: {
      get () {
        return [this.datetime.getHours(), this.datetime.getMinutes(), this.datetime.getSeconds()].join(':')
      },
      set (value) {
        const [ hours, minuts, seconds ] = value.split(':')
        this.updateTime(hours, minuts,seconds);
      }
    }
  },
  created () {

    if (!this.value) {
      this.$emit('input', this.datetime)
    } else {
     
      this.datetime = new Date(this.value)
    }

  }
}
</script>

